@import "https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
}

body {
  background-color: #eaf6f6;
}

form h2, h1, .username, h3 {
  color: #428f8d;
}

label img {
  width: 50px;
}

#loginForm, #createUserForm {
  width: 350px;
  background-color: #e8d9d9;
  border: 5px solid #a0c2c2;
  border-radius: 10px;
  flex-direction: column;
  margin: 20px auto;
  padding: 1em 1.5em;
  display: flex;
}

#loginForm input, #createUserForm input, #newPostForm input {
  background-color: #f0f0f0;
  border: none;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: .6em;
  font-size: 1rem;
}

#loginForm button, #createUserForm button, #newPostForm button {
  cursor: pointer;
  color: #fff;
  background-color: #a0c2c2;
  border: none;
  border-radius: 5px;
  padding: .6em;
  font-size: 1rem;
  transition: all .2s ease-in-out;
}

#loginForm button:hover, #createUserForm button:hover, #newPostForm button:hover {
  background-color: #75a4a4;
}

#loginForm h2, #createUserForm h2 {
  margin-bottom: 10px;
}

#createUserForm div {
  text-align: center;
}

.header {
  background-color: #e8d9d9;
  border-bottom: 5px solid #a0c2c2;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  display: flex;
}

h1 {
  margin-left: 5vw;
}

.feed-container {
  justify-content: space-around;
  margin-top: 20px;
  display: flex;
}

.feed {
  max-width: 70%;
}

.nav-info {
  justify-content: flex-end;
  margin-right: 3vw;
  display: flex;
}

.nav-info button, .nav-info div {
  margin: 0 10px 0 0;
}

.nav-info button {
  cursor: pointer;
  color: #fff;
  background-color: #a0c2c2;
  border: none;
  border-radius: 6px;
  padding: .5em;
  font-size: 1rem;
  transition: all .2s ease-in-out;
}

.nav-info button:hover {
  background-color: #75a4a4;
}

.userinfo {
  align-items: center;
  display: flex;
}

.userinfo > p {
  color: #000000ca;
  margin-right: 5px;
}

.userinfo > img {
  width: 30px;
}

.all-users {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.all-users a {
  cursor: pointer;
  width: auto;
  color: #75a4a4;
  margin: 5px 0;
  text-decoration: underline;
}

.post {
  border: 5px solid #a0c2c2;
  border-radius: 4px;
  margin: 10px 0;
}

.userNameAndPic {
  background-color: #e8d9d9;
  align-items: center;
  padding-left: 10px;
  display: flex;
}

.username {
  padding: .5em 1em;
  font-weight: bold;
}

.profilepic {
  height: 30px;
}

.message {
  color: #000000c1;
  background-color: #f0f0f0;
  padding: .5em 1em;
}

.timestamp {
  text-align: right;
  color: #00000082;
  background-color: #f0f0f0;
  padding: .5em 1em;
  font-size: .9em;
  font-style: italic;
}

#newPostForm input {
  border: 5px solid #e8d9d9;
}

.profile-container {
  justify-content: space-around;
  display: flex;
}

.user-card {
  background-color: #e8d9d9;
  border: 5px solid #a0c2c2;
  margin-top: 10px;
  padding: 1em;
}

.user-card h3 {
  text-align: center;
  margin-bottom: 10px;
}

#clicked-user-posts, .profile-posts {
  max-width: 60%;
}

#newPostForm {
  margin-top: 10px;
}

#newPostForm button {
  cursor: pointer;
}

.popup {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #0006;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.popup-content {
  width: 50%;
  text-align: center;
  background-color: #eff3f4;
  border: 1px solid #727a7e;
  margin: 15% auto;
  padding: 20px;
  position: fixed;
  top: 20px;
  left: 25vw;
}

.popup-buttons button {
  margin-top: 20px;
}

